import React, { Component } from 'react'
import {itemsList,shuffle,initializeDataController} from './controllers/DataController'

const ProductContext = React.createContext()

class ProductProvider extends Component {

    state={
        currentIndex :0,
        currentGroupId :0,
        totalSavings :0,
        userCart :[],
        isGameOver:false,
        screenName:'splash',
        
    }

    componentDidMount(){
        initializeDataController();
    }

    updateCurrentIndex=(itemId)=>{
        // console.log(" called" + this.state.currentIndex)
        this.setState({
            currentIndex:(this.state.currentIndex+1)%15,
            userCart : [...this.state.userCart,itemId]
        })

        
    }

    updateScreenName=(value)=>{
        this.setState({
            screenName: value
        })
    }
    

    // setPoducts = ()=>{
    //     let tempProducts = []
    //     this.setState(()=>{
    //         return {products:tempProducts}
    //     })
    // }

    getItem = (id)=>{
        
    }

    


    render() {
        return (
            <ProductContext.Provider value={{
                ...this.state,
                getItem: this.getItem,
                updateCurrentIndex:this.updateCurrentIndex,
                updateScreenName:this.updateScreenName,
                
            }}>
                {this.props.children}
            </ProductContext.Provider>
        )
    }
}


const ProductConsumer = ProductContext.Consumer;

export {ProductConsumer,ProductProvider}
